
import { mapGetters } from 'vuex'
import cookie from '../../../static/js/cookie'
import { wxTools } from '../../utils/wxTools'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

export default {
name: 'Center',
data () {
return {
kefu: config.KEFU_CENTER,
kaiyun: config.KAIYUBN_CENTER,
kecheng: config.KECHENG_CENTER,
xuyuan: config.XUYUAN_CENTER,
tid: cookie.getCookie('tid'),
userInfo: [],
nickname: sessionStorage.getItem('nickname'),
avatar: sessionStorage.getItem('avatar')
}
},
created () {


let token = cookie.getCookie('token')
if (token === null) {
sessionStorage.setItem("url", this.$route.path);
this.getWechatAuth()
}
},
computed: {
...mapGetters({
setInfo: 'userInfo'
})
},
methods: {
getWechatAuth: function () {
return this.$api.wechatAuth().then(res => {
window.location.href = res.data.result
}).catch(err => {
return err
})
},
}
}



